img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

li {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

:root {
  font-size: calc(0.5em + 1vw);
  line-height: 1.3;
  box-sizing: border-box;
  font-family: "Bitter", "Arial", serif;
  font-weight: normal;
  font-display: swap;
  height: 100%;
  background-color: var(--page-color);
  color: var(--text-page-color);
  overflow-x: hidden;
}
@media (max-width: 600px) {
  :root {
    font-size: 14px;
  }
}
@media (min-width: 1200px) {
  :root {
    font-size: 20px;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 1.6rem;
}

button {
  cursor: pointer;
  border: none;
}

*,
::before,
::after {
  box-sizing: inherit;
}

svg path {
  pointer-events: auto;
}

.double-container {
  max-width: 1480px;
  margin: 0 auto;
  padding-bottom: 1px;
}

.header__container {
  position: absolute;
  z-index: 1;
  height: 3em;
  width: 100vw;
  left: 50%;
  margin-left: -50vw;
  background-color: var(--container-color);
}

.header {
  height: 3em;
  max-width: 1480px;
  margin: 0 auto;
  background-color: transparent;
}
@media (min-width: 768px) {
  .header {
    display: flex;
    align-items: center;
  }
}

.header__nav {
  position: absolute;
  top: 0;
}

.jsMenuClose::after {
  position: absolute;
  content: " ";
  width: 100%;
  height: 2.5px;
  background-color: var(--line-color);
}
@media (min-width: 768px) {
  .jsMenuClose::after {
    bottom: 0;
  }
}

@media (min-width: 768px) {
  .body__header {
    padding: 0 1.7rem;
  }
}

.nav {
  display: block;
  position: relative;
  align-items: center;
  margin: 0;
  height: 100%;
  width: 100%;
  background-color: var(--container-color);
}
@media (min-width: 768px) {
  .nav {
    display: flex;
  }
}

.nav__list-container {
  order: -1;
}

.nav__list {
  background-color: var(--container-color);
}
@media (max-width: 767.9px) {
  .nav__list {
    width: max-content;
    padding: 0.3em 3em 1em 2em;
  }
}
@media (min-width: 768px) {
  .nav__list {
    display: flex;
    flex-wrap: wrap;
    padding: 0;
  }
}

.nav__link {
  display: block;
  padding: 0.5em;
}

@media (max-width: 767.9px) {
  .nav--close {
    display: none;
  }
}

.nav__link {
  transition: opacity 0.45s;
  color: var(--link-color);
}

.nav__item + .nav__item {
  margin-top: 0.5em;
}
@media (min-width: 768px) {
  .nav__item + .nav__item {
    margin: 0 0 0 0.9rem;
  }
}

.nav__btn-wrapper {
  margin: 0 auto;
  max-width: 1440px;
  height: 3em;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1.7rem;
}
@media (min-width: 768px) {
  .nav__btn-wrapper {
    padding: 0;
    margin: 0 0 0 auto;
  }
}

.nav__btn {
  margin: 0;
  padding: 0;
  background-color: transparent;
  display: flex;
}

.nav__btn svg path {
  transition: opacity 0.45s;
}

.icon {
  color: var(--link-color);
  transition: color 0.25s;
}

.icon:hover {
  color: var(--btn-hover-color);
}

.icon:active {
  opacity: 0.3;
}

.nav__btns-theme {
  display: flex;
}

.nav__btn-theme + .nav__btn-theme {
  margin-left: 0.4em;
}

.nav__btn--active .js-icon-theme {
  color: var(--icon-hover-color);
}

.header__btn svg g path {
  transition: fill 0.25s;
}

.header__btn:hover svg g path {
  fill: var(--icon-hover-color);
}

.nav__btn {
  background-color: transparent;
}

@media (min-width: 768px) {
  .nav__btn-menu {
    display: none;
  }
}

@media (max-width: 767.9px) {
  .nav--close {
    display: none;
  }
}

.main {
  margin: 0 1.7em;
  padding-top: 3em;
}
@media (max-width: 767.9px) {
  .main {
    padding-top: 10em;
  }
}

.bio {
  display: grid;
  width: 100%;
  column-gap: 1.75em;
  row-gap: 0.65em;
  padding: 0;
  margin: 0;
}
@media (max-width: 767.9px) {
  .bio {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(5, auto);
    text-align: center;
    justify-items: center;
    grid-template-areas: "header" "picture" "subhead" "text" "links";
  }
}
@media (min-width: 768px) {
  .bio {
    grid-template-columns: 1fr 2fr;
    grid-template-rows: repeat(4, auto);
    grid-template-areas: "picture header" "picture subhead" "picture text" "picture links";
  }
}
@media (min-width: 1280px) {
  .bio {
    grid-template-columns: 1fr 2fr auto;
    grid-template-areas: "picture header header" "picture subhead subhead" "picture text links" "picture text links";
  }
}

.bio__picture-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-area: picture;
  background-color: var(--line-color);
  border-radius: 1.5em;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.bio__picture-wrapper img {
  display: flex;
  flex-grow: 1;
  border-radius: 1.5em;
  object-fit: cover;
  object-position: top;
  max-height: 15.5em;
}
@media (max-width: 1279.9px) {
  .bio__picture-wrapper img {
    max-height: 19em;
  }
}
@media (max-width: 767.9px) {
  .bio__picture-wrapper img {
    max-height: 13em;
  }
}

.bio__header {
  margin: 0;
  padding-top: 0.5em;
  font-size: 2rem;
  font-weight: bold;
  grid-area: header;
}

.bio__subhead {
  margin: 0;
  font-weight: bold;
  grid-area: subhead;
}

.bio__text {
  margin: 0;
  grid-area: text;
}
@media (min-width: 1280px) {
  .bio__text {
    padding-bottom: 1em;
  }
}

.bio__links-list {
  margin: 0 0 0 auto;
  padding-bottom: 1em;
  grid-area: links;
  width: max-content;
}
@media (max-width: 767.9px) {
  .bio__links-list {
    margin: 0 auto;
  }
}
@media (min-width: 1280px) {
  .bio__links-list {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-between;
  }
}

.section-container {
  position: relative;
  width: 100vw;
  left: 50%;
  margin-left: -50vw;
  background-color: var(--container-color);
}

.section {
  max-width: 1480px;
  width: 100%;
  padding: 2em 1.7em;
  margin: 0 auto;
  color: var(--text-container-color);
  background-color: transparent;
}

.section__header {
  display: flex;
  justify-content: center;
  margin: 0 0 2rem 0;
  color: var(--header-container-color);
}

.skills__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-auto-flow: dense;
  grid-auto-rows: 1fr;
  justify-content: center;
  row-gap: 0.7rem;
  border: 3px solid var(--line-color);
  border-radius: 3rem;
}
@media (max-width: 767.9px) {
  .skills__list {
    border: none;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: repeat(7, auto);
    row-gap: 2rem;
  }
}

@media (min-width: 768px) {
  .skills__list .skills__item:last-child {
    grid-column: 2 span;
    justify-self: center;
    align-self: center;
    text-align: center;
    padding: 0 5rem;
    flex-direction: column;
    border-bottom: none;
  }
  .skills__list .skills__item:last-child .skill__svg-container {
    flex-direction: row;
    order: 1;
  }
  .skills__list .skills__item:last-child::before {
    top: -2.8rem;
  }
}

@media (min-width: 768px) {
  .skills__list .skills__item:nth-child(1)::before,
  .skills__list .skills__item:nth-child(2)::before {
    display: none;
  }
}

.skills__item {
  position: relative;
  padding: 0 1.5em;
}
@media (max-width: 767.9px) {
  .skills__item {
    grid-column: 3 span;
    grid-row: 1 span;
  }
}

.skills__item::before {
  content: " ";
  position: absolute;
  display: block;
  width: 100%;
  height: 2px;
  top: -0.4rem;
  left: 0;
  background-color: var(--line-color);
}
@media (max-width: 767.9px) {
  .skills__item::before {
    width: 100vw;
    top: -1rem;
    left: 50%;
    margin-left: -50vw;
  }
}

@media (max-width: 767.9px) {
  .skills__list .skill:last-child::after {
    content: " ";
    position: absolute;
    display: block;
    width: 100vw;
    height: 2px;
    bottom: -1rem;
    left: 50%;
    margin-left: -50vw;
    background-color: var(--line-color);
  }
}

.skills__item-text {
  margin: 0;
}

.skill {
  display: flex;
  align-items: center;
  flex-shrink: 0;
  width: auto;
  line-height: 1.4;
}

.skill__svg-container {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .skill__svg-container {
    align-items: end;
  }
}

.skill svg {
  min-height: 1.5rem;
  max-height: 1.5rem;
  min-width: 1.5rem;
  max-width: 1.5rem;
  margin-right: 1rem;
}

.skill img {
  user-select: none;
  margin-top: 0.5rem;
  width: 1.5rem;
  height: 1.5rem;
}

.skill__icon-svg {
  color: var(--svg-sprite);
}

.skill__icon-svg--redux {
  color: var(--svg-sprite-redux);
}

.skill svg + svg {
  margin-top: 0.5rem;
}

.education {
  padding: 2rem 0;
}

.education__header {
  margin-top: 0;
  display: flex;
  justify-content: center;
}

.education__item-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .education__item-container {
    display: grid;
    grid-template-columns: 1fr 9fr;
    grid-template-rows: repeat(4, auto);
    grid-column-gap: 1.5rem;
    justify-items: start;
    row-gap: 0.5rem;
  }
}

.education__item-picture {
  max-width: 7em;
  height: auto;
}
@media (min-width: 768px) {
  .education__item-picture {
    align-self: start;
    grid-column: 1/2;
    grid-row: 1/5;
  }
}

.education__item-header {
  margin: 0.8em 0 0.4em 0;
  font-weight: bold;
  font-size: 1.04em;
  color: var(--text-page-color);
}
@media (min-width: 768px) {
  .education__item-header {
    grid-column: 2/3;
    grid-row: 1 span;
    margin: 0;
  }
}

.education__item-header:hover {
  color: var(--btn-hover-color);
}

.education__item-date {
  font-size: 0.8rem;
  line-height: 0.9;
  margin: 0 0 0.7em 0;
}
@media (min-width: 768px) {
  .education__item-date {
    grid-column: 2/3;
    grid-row: 1 span;
    margin: 0;
  }
}

.education__item-text {
  margin: 0;
  text-align: center;
}
@media (min-width: 768px) {
  .education__item-text {
    grid-column: 2/3;
    grid-row: 1 span;
    text-align: start;
  }
}

.education__item-btn {
  grid-column: 2/3;
  margin: 0 0 0 auto;
  background-color: transparent;
}

.education__item-btn svg {
  display: block;
  width: 1.67rem;
  height: 1.67rem;
}

.education__item-btn svg path,
.education__item-btn svg circle {
  stroke: var(--icon-color);
}

.education__item-btn:hover:not(.disabled) svg path,
.education__item-btn:hover:not(.disabled) svg circle {
  stroke: var(--icon-hover-color);
}

.education__item + .education__item {
  margin-top: 1.3em;
}

@media (min-width: 1280px) {
  .projects {
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: auto;
    column-gap: 3em;
    grid-template-areas: "header header" "btns projects" "postscripts postscripts";
  }
}

@media (min-width: 1280px) {
  .projects__header {
    grid-area: header;
  }
}

.projects__btn-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
@media (min-width: 1280px) {
  .projects__btn-list {
    position: sticky;
    top: 5em;
    grid-area: btns;
    border: 1px solid var(--line-color);
    max-width: 13em;
    max-height: 6em;
    border-radius: 1.25em;
    padding: 0.4em;
  }
}

.projects__btn {
  display: block;
  background-color: transparent;
  padding: 0.5em;
  margin: 0.3em;
  color: var(--link-color);
  transition: color 0.25s;
  cursor: pointer;
}
@media (min-width: 768px) {
  .projects__btn {
    padding: 0;
    margin: 0;
  }
}

.projects__btn:hover {
  color: var(--icon-hover-color);
}

.projects__btn:checked + label {
  color: var(--icon-hover-color);
}

@media (min-width: 1280px) {
  .projects__list {
    grid-area: projects;
  }
}
.projects__list .projects__item + .projects__item {
  margin-top: 2em;
}

@media (max-width: 1279.9px) {
  .projects__item {
    margin-top: 2em;
  }
}

.projects__postscript-wrapper {
  max-width: 1480px;
  margin: 0 auto;
  padding: 0 1.7rem;
}

.projects__postscript {
  text-align: center;
  margin: 0;
  padding: 1rem 0 2.5rem 0;
  color: var(--text-container-color);
}
@media (min-width: 1280px) {
  .projects__postscript {
    grid-area: postscripts;
  }
}

.projects__github-link {
  color: var(--line-color);
  transition: color 0.4s;
}

.projects__github-link:hover {
  color: var(--icon-hover-color);
}

.project-card {
  display: grid;
  grid-template-columns: 1fr auto;
  grid-template-rows: repeat(6, auto);
  color: var(--text-container-color);
  padding: 0 1rem;
  border-radius: 1.5rem;
  box-shadow: -3px 3px 0 3px var(--line-color);
  border: 3px solid var(--line-color);
  margin: 0;
}
@media (max-width: 767.9px) {
  .project-card {
    box-shadow: -2px 2px 0 1px var(--line-color);
  }
}

.project-card__image {
  margin-top: 1rem;
  width: 100%;
  max-height: 23em;
  justify-content: center;
  overflow: hidden;
  grid-column: 1/3;
  grid-row: 1 span;
  border-radius: 1.5rem;
}

.project-card__image img {
  width: 100%;
  height: 100%;
  background-color: var(--substrate-color);
  object-fit: cover;
  object-position: top;
  margin: 0 auto;
  border-radius: 1.5rem;
}
@media (max-width: 767.9px) {
  .project-card__image img {
    object-fit: contain;
  }
}

.project-card__name {
  grid-column: 1/3;
  grid-row: span 1;
  margin: 0.7rem 0 0 0;
  font-size: 1.3rem;
  font-weight: bold;
}

.project-card__caption {
  margin: 0 0 0.2rem 0;
  grid-column: 1/2;
  grid-row: span 1;
  color: var(--line-color);
}

.stack-list {
  margin: 0;
}

.project-card__desc {
  margin: 0 0 1rem 0;
  line-height: 1.4;
  grid-column: 1/3;
  grid-row: 5/6;
  text-align: left;
}
@media (max-width: 767.9px) {
  .project-card__desc {
    grid-column: 1/3;
    grid-row: 4/5;
  }
}

.project-card__links {
  grid-column: 2/3;
  grid-row: 4/5;
}
@media (max-width: 767.9px) {
  .project-card__links {
    grid-column: 1/3;
    grid-row: 5/6;
    margin: 0 auto;
  }
}

.icons-list {
  display: flex;
  margin: 0.5rem 0;
  grid-column: 1/2;
  grid-row: 4/5;
}
@media (max-width: 767.9px) {
  .icons-list {
    grid-column: 1/3;
    grid-row: 6/7;
    justify-content: center;
    margin: 0.75rem 0;
  }
}

.icons-list li {
  line-height: 0;
}

.icons-list svg {
  height: 1.15rem;
  width: 1.15rem;
}

.icons-list li + li {
  margin-left: 0.4rem;
}

.project-card__links {
  align-items: center;
}

.links-list {
  display: flex;
}

.links-list__item + .links-list__item {
  margin-left: 0.7rem;
}

.links-list__link svg {
  display: block;
  height: 1.57rem;
  width: 1.57rem;
}

.links-list__link path {
  transition: fill 0.25s;
}

.links-list__link:hover:not(.disabled) path {
  fill: var(--icon-hover-color);
}

.modal {
  position: fixed;
  z-index: 3;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--modal-color);
  border: none;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.7);
  margin: 0;
  max-width: 85vw;
  max-height: 85vh;
  overflow: scroll;
}

.modal__btn::after,
.modal__btn::before {
  content: " ";
  display: block;
  position: absolute;
  right: 0.01em;
  top: 0.4rem;
  width: 1rem;
  height: 2px;
  rotate: 45deg;
  background-color: white;
}

.modal__btn::before {
  rotate: -45deg;
}

.modal__btn {
  z-index: 1;
  display: block;
  position: absolute;
  width: 1rem;
  height: 1rem;
  top: 0.5rem;
  right: 1em;
  background-color: transparent;
}

.modal__btn:hover::before,
.modal__btn:hover::after {
  background-color: var(--line-color);
}

.modal__img {
  width: 100%;
  height: 100%;
  padding: 0 2rem;
  object-fit: contain;
}

.overlay {
  display: none;
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0));
  backdrop-filter: blur(3px);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.5);
}

.showIt {
  display: block;
}

.features {
  max-width: 1480px;
  margin: 0 auto;
  padding: 2rem 0;
}
@media (min-width: 1280px) {
  .features {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 2.5em;
    grid-template-areas: "header header" "feature-side feature-soft";
  }
}

.features__header {
  margin: 0;
  display: flex;
  justify-content: center;
  margin-bottom: 1em;
}
@media (min-width: 1280px) {
  .features__header {
    grid-area: header;
  }
}

@media (min-width: 1280px) {
  .feature-side {
    grid-area: feature-side;
  }
}

@media (min-width: 1280px) {
  .feature-soft {
    grid-area: feature-soft;
  }
}

.feature + .feature {
  margin-top: 2em;
}
@media (min-width: 1280px) {
  .feature + .feature {
    margin-top: 0;
  }
}

.feature {
  border-radius: 1.4em;
  padding: 1.5em 0.7em 1em;
  background-color: var(--container-color);
}

.feature__tittle {
  text-align: center;
  font-weight: bold;
  font-size: 1.3rem;
  margin: 0 0.3em 0.15em;
  color: var(--text-container-color);
}

.feature__button {
  display: block;
  background-color: transparent;
  border-bottom: 1px dashed var(--text-container-color);
  margin: 0 auto;
  margin-bottom: 1.2em;
  color: var(--text-container-color);
}

.feature__button:hover {
  color: var(--icon-hover-color);
  border-color: var(--icon-hover-color);
}

.feature__list {
  margin-top: 0.7em;
}

.feature__list .feature__item + .feature__item {
  margin-top: 0.5em;
}

.feature__dropdown {
  border-radius: 0.7em;
  padding: 0.1em;
  background-color: var(--page-color);
}

.feature__summary {
  text-align: left;
  position: relative;
  font-weight: 500;
  cursor: pointer;
  padding: 0.3em 2em;
  font-size: 1.05rem;
  color: var(--text-page-color);
  transition: color 0.3s;
}

.feature__summary:hover {
  color: var(--icon-hover-color);
}

.feature__summary::marker {
  content: " ";
}

.feature__summary::before {
  content: " ";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0.3em;
  width: 1em;
  height: 1em;
  background-image: url("../img/arrow.svg");
  background-size: 1em 1em;
  transition: transform 0.5s;
}

.feature__summary.expended::before {
  transform: rotate(90deg) translateX(-50%);
}

.feature__content {
  padding: 0.3em;
  border-radius: 0.7em;
  background-color: var(--page-color);
}

.feature__text {
  margin: 0 0.6em 0.4em 0.6em;
  color: var(--text-page-color);
}

.footer {
  display: block;
  margin-bottom: 2em;
}

.footer .links-list {
  justify-content: center;
}

.svg-color {
  fill: var(--icon-color);
}

.svg-color:hover {
  fill: var(--icon-hover-color);
}

.hidden {
  display: none;
}

.disabled {
  cursor: auto;
  opacity: 0.5;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@media print {
  * {
    color: black !important;
    background: none !important;
  }
  .header,
  .header__container,
  .footer,
  .nav {
    display: none !important;
  }
  .main {
    padding: 0 !important;
  }
}